import { Actions } from '../../../types';
export const updateLFO1Target = (target) => ({
    type: Actions.UPDATE_LFO_1_TARGET,
    payload: target,
});
export const updateLFO2Target = (target) => ({
    type: Actions.UPDATE_LFO_2_TARGET,
    payload: target,
});
export const switchDelay = (active) => ({
    type: Actions.SWITCH_DELAY,
    payload: active,
});
