import { Actions } from '../../../../types';
export const updateCompressorThreshold = (threshold) => ({
    type: Actions.UPDATE_COMPRESSOR_THRESHOLD,
    payload: threshold,
});
export const updateCompressorRatio = (ratio) => ({
    type: Actions.UPDATE_COMPRESSOR_RATIO,
    payload: ratio,
});
export const updateCompressorKnee = (knee) => ({
    type: Actions.UPDATE_COMPRESSOR_KNEE,
    payload: knee,
});
export const updateCompressorAttack = (attack) => ({
    type: Actions.UPDATE_COMPRESSOR_ATTACK,
    payload: attack,
});
export const updateCompressorRelease = (release) => ({
    type: Actions.UPDATE_COMPRESSOR_RELEASE,
    payload: release,
});
