import { round } from 'lodash';
import React, { useContext, useEffect } from 'react';
import { knobsValues } from '../../../../constants/knobsValues';
import { useBreakpoints } from '../../../../hooks/useBreakpoints';
import { midiContext } from '../../../../providers/MIDIProvider';
import { Knobs } from '../../../../types';
import { envelopeStateContext } from '../../EnvelopeControls/EnvelopeStateProvider';
import { updatePitchbend } from '../../EnvelopeControls/store/actions';
import styles from './index.module.scss';
export const PitchbendWheel = () => {
    const { selectedInput: midiInput } = useContext(midiContext);
    const { state, dispatch } = useContext(envelopeStateContext);
    const { isDesktop } = useBreakpoints();
    useEffect(() => {
        if (midiInput) {
            midiInput.addListener('pitchbend', (e) => {
                const multiplier = (knobsValues[Knobs.PITCHBEND].max -
                    knobsValues[Knobs.PITCHBEND].min) /
                    2;
                const value = round(e.value * multiplier, 2);
                dispatch(updatePitchbend(value));
            });
        }
    }, [midiInput]);
    const handleChange = (e) => {
        const pitchbendValue = round(e.target.value, 2);
        dispatch(updatePitchbend(pitchbendValue));
    };
    const handleDragEnd = () => {
        dispatch(updatePitchbend(0));
    };
    return (React.createElement("div", { id: styles.pitchbend_wheel_container },
        React.createElement("input", { id: styles.pitchbend_wheel, className: isDesktop ? styles.desktop : '', "data-testid": "pitchbend_wheel", type: "range", min: knobsValues[Knobs.PITCHBEND].min, max: knobsValues[Knobs.PITCHBEND].max, step: knobsValues[Knobs.PITCHBEND].step, value: state.pitchbend, onChange: handleChange, onMouseUp: handleDragEnd })));
};
export default PitchbendWheel;
