import { Actions } from '../../../../types';
export const updateDelayFeedback = (feedback) => ({
    type: Actions.UPDATE_DELAY_FEEDBACK,
    payload: feedback,
});
export const updateDelayTime = (delayTime) => ({
    type: Actions.UPDATE_DELAY_TIME,
    payload: delayTime,
});
export const switchDelayTrails = (trails) => ({
    type: Actions.SWITCH_DELAY_TRAILS,
    payload: trails,
});
